import React from 'react'
import Helmet from 'react-helmet'
import Link from 'gatsby-link'

const Project1 = () => (
  <div className="page Project1-page">
    <Helmet
      title="Raptor Project, Kacper Cieciński Junior Web Developer"
      meta={[
        { name: 'description', content: 'Raptor project by Kacper Ciecinski' },
        { name: 'keywords', content: 'Kacper, Cieciński, Kacper Cieciński, Ciecinski, developer, junior developer,junior react developer, junior front-end developer,front-end,frontend,backend,junior backend developer,junior rails developer,React,Ruby on Rails,Javascript, HTML5, CSS,Raptor, 314Apps, 314Apps intern, 314Apps internship, Ruby on Rails Project, RoR Incubator, Incubator ' },
     ]}
    />

    <h1>Raptor </h1>
    <div className="row row-center">
      <div className="col-md-10">
      <p>Raptor is a resource managment system developed by me and one other intern with help and mentorship from Senior Developer, as an internship project for 314apps <br/>
      It is already in use by company and we still develop new features. It's written in Ruby on Rails and on front we used JQuery. <br/>
      Developing this project really helped me become better programmer as in such big project I encounter some problem for first time, like optimization problems or how important UX is. <br/>
      It also gave me oportunity to use cool modern Ruby features like ActionCable, as well as chance to see how should good, scalable architecture  for Rails app look.
      </p>
      <div className="info-container">
        <p>
          I cannot provide code or demo for this project.
        </p>
      </div>
      <Link className="project-link" to="/projects">Back to projects</Link>
      </div>
    </div>

  </div>
)

export default Project1


